<template>
  <div class="form-group">
    <label :for="id">{{ label }}</label>
    <input 
      :type="type" 
      :id="id" 
      :value="modelValue" 
      @input="updateValue" 
      :required="required" 
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';

export default defineComponent({
  name: 'FormInput',
  props: {
    id: String,
    label: String,
    type: {
      type: String as PropType<'text' | 'password' | 'email'>,
      default: 'text'
    },
    modelValue: String,
    required: Boolean
  },
  emits: ['update:modelValue'],
  methods: {
    updateValue(event: Event) {
      const input = event.target as HTMLInputElement;
      this.$emit('update:modelValue', input.value);
    }
  }
});
</script>

<style scoped>
.form-group {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 20px;
}

label {
  margin-bottom: 8px;
  color: #555;
  font-weight: bold;
  font-size: 14px;
  text-align: left;
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  width: 100%;
}
</style>
