<template>
  <div class="login">
    <img src="../assets/logo.jpeg" class="logo" alt="Logo" />
    <h2>Login</h2>
    <form @submit.prevent="handleLogin">
      <div>
        <label for="username">Username:</label>
        <input type="text" id="username" v-model="username" required autocomplete="off" />
      </div>
      <div>
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="password" required />
      </div>
      <div class="options">
        <label for="rememberMe" class="remember-me-label">
          <input type="checkbox" id="rememberMe" v-model="rememberMe" />
          Remember Me
        </label>
        <a @click="goToForgotPassword" class="forgot-password">Forgot Password?</a>
      </div>
      <button type="submit">Login</button>
      <div class="divider">
        <span class="or">or</span>
      </div>
      <div class="social-login">
        <button @click="redirectTo('google')" class="social-button google">Login with Google</button>
        <button @click="redirectTo('facebook')" class="social-button facebook">Login with Facebook</button>
      </div>
    </form>
    <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
    <p class="signup-link">
      Don't have an account? <router-link to="/signup">Sign Up</router-link>
    </p>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import { useRouter } from 'vue-router';

export default defineComponent({
  name: 'LoginPage',
  setup() {
    const username = ref<string>('');
    const password = ref<string>('');
    const rememberMe = ref<boolean>(false);
    const errorMessage = ref<string>('');
    const router = useRouter();

    const handleLogin = () => {
      // Kullanıcı adı ve şifre doğrulaması yapılmadan her zaman başarılı
      alert('Login successful!');
      errorMessage.value = '';
      // Handle remember me functionality
      console.log('Remember Me:', rememberMe.value);
      router.push('/main'); // Yönlendirme
    };

    const redirectTo = (platform: string) => {
      if (platform === 'google') {
        window.location.href = 'https://accounts.google.com/signin'; // Google login page URL
      } else if (platform === 'facebook') {
        window.location.href = 'https://www.facebook.com/login'; // Facebook login page URL
      }
    };

    const goToForgotPassword = () => {
      router.push('/forgot-password'); // Yönlendirme URL'sini güncelleyin
    };

    return {
      username,
      password,
      rememberMe,
      errorMessage,
      handleLogin,
      redirectTo,
      goToForgotPassword
    };
  }
});
</script>

