  <div class="blue-box">
    <div class="blue-box-content">
      <h2>Statistics</h2>
      <p class="subtitle">Revenue</p>
      <transition name="fade" @before-enter="beforeEnter" @enter="enter" @leave="leave">
        <div class="chart-container">
          <BarChart 
            :labels="['2024-8-9', '2024-8-10', '2024-8-11', '2024-8-12', '2024-8-13', '2024-8-14']"
            :data="[350, 200, 125, 250, 261, 196]"
            borderColor="white" 
            pointBackgroundColor="white"
            backgroundColor="rgba(255, 255, 255, 255)"
            :barPercentage="0.4"  
            :categoryPercentage="0.4"  
            :showAxes="true"
            :yAxisMin="50" 
            :yAxisMax="350"
            :duration="1000"
            :showLine="true"
            :borderRadius="15"
            :lineColor="'#000000'"
          />
        </div>
      </transition>
    </div>
  </div>
  <ProgressBox />
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import BarChart from '@/components/BarChart.vue';

export default defineComponent({
  name: 'StatisticsPage',
  components: {
    BarChart,
  },
  methods: {
    beforeEnter(el: Element) {
      (el as HTMLElement).style.opacity = '0';
    },
    enter(el: Element, done: () => void) {
      (el as HTMLElement).offsetHeight; // Trigger reflow
      (el as HTMLElement).style.transition = 'opacity 1s';
      (el as HTMLElement).style.opacity = '1';
      done();
    },
    leave(el: Element, done: () => void) {
      (el as HTMLElement).style.transition = 'opacity 1s';
      (el as HTMLElement).style.opacity = '0';
      done();
    }
  }
});
</script>

