<template>
  <div>
    <canvas ref="barChart"></canvas>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from 'vue';
import { Chart, registerables, ChartTypeRegistry } from 'chart.js';

Chart.register(...registerables);

export default defineComponent({
  name: 'BarChart',
  props: {
    labels: {
      type: Array as () => string[],
      default: () => []
    },
    data: {
      type: Array as () => number[],
      default: () => []
    },
    barPercentage: {
      type: Number,
      default: 0.8
    },
    categoryPercentage: {
      type: Number,
      default: 0.6
    },
    showAxes: {
      type: Boolean,
      default: true
    },
    yAxisMin: {
      type: Number,
      default: 0
    },
    yAxisMax: {
      type: Number,
      default: 100
    },
    showLine: {
      type: Boolean,
      default: false
    },
    colors: {
      type: Array as () => string[],
      default: () => ['#ADD8E6']
    },
    borderRadius: {
      type: Number,
      default: 0
    },
    barThickness: {
      type: Number,
      default: 5
    },
    pointSpacing: {
      type: Number,
      default: 10
    },
    useGradient: {
      type: Boolean,
      default: true
    }
  },
  setup(props) {
    const barChart = ref<HTMLCanvasElement | null>(null);

    const getGradient = (ctx: CanvasRenderingContext2D | null) => {
      if (!ctx) return '#FFFFFF';

      const gradient = ctx.createLinearGradient(0, 0, 0, 400);

      const opacityStart = 0;
      const opacityEnd = 1;

      gradient.addColorStop(0.3, `rgba(255, 255, 255, ${opacityStart})`);
      gradient.addColorStop(0.1, `rgba(255, 255, 255, ${opacityEnd})`);
      
      return gradient;
    };
    onMounted(() => {
      if (barChart.value) {
        const ctx = barChart.value.getContext('2d');

        new Chart(barChart.value, {
          type: 'bar' as keyof ChartTypeRegistry,
          data: {
            labels: props.labels,
            datasets: [
              {
                type: 'bar' as keyof ChartTypeRegistry,
                data: props.data,
                backgroundColor: (context: any) => {
                  return props.useGradient ? getGradient(ctx) : props.colors[0];
                },
                borderRadius: props.borderRadius,
                borderSkipped: false,
                barPercentage: props.barPercentage,
                categoryPercentage: props.categoryPercentage,
                barThickness: props.barThickness,
              },
              ...(props.showLine
                ? [
                    {
                      type: 'line' as keyof ChartTypeRegistry,
                      data: props.data.map((value) => value + 50),
                      borderColor: '#FFFFFF',
                      borderWidth: 2,
                      tension: 0.4,
                      fill: false,
                      pointRadius: 4,
                      pointBackgroundColor: '#FFFFFF',
                      pointBorderColor: '#FFFFFF',
                      pointHoverRadius: 6,
                      pointStyle: 'circle',
                      // Animasyon buradan kaldırıldı
                    },
                  ]
                : []),
            ],
          },
          options: {
            responsive: true,
            maintainAspectRatio: false,
            animation: {
              duration: 1000, // 1 saniyelik animasyon süresi
              easing: 'linear', // Geçerli bir easing değeri kullanıldı
            },
            plugins: {
              legend: {
                display: false,
              },
            },
            scales: {
              x: {
                display: props.showAxes,
                grid: {
                  display: false,
                },
                ticks: {
                  maxTicksLimit: 10,
                  autoSkip: true,
                },
              },
              y: {
                display: props.showAxes,
                grid: {
                  display: false,
                },
                beginAtZero: true,
                min: props.yAxisMin,
                max: props.yAxisMax + 50,
                ticks: {
                  stepSize: 50,
                },
              },
            },
          },
        });
      }
    });

    return { barChart };
  }
});
</script>

<style scoped>
/* Eğer özel stil gerekiyorsa Tailwind CSS ile yapabilirsiniz */
</style>
