<template>
  <div class="secondary-header">
    <div class="logo-container">
      <img src="../assets/logo.jpeg" alt="Logo" class="logo" />
      <span class="logo-text">Pragmatic</span>
    </div>

    <!-- SearchComponent -->
    <div class="search-container">
      <div class="search-input">
        <input 
          type="text"
          v-model="query"
          @keydown.enter="onSearch"
          placeholder="Search.."
        />
        <i class="fa-solid fa-search search-icon"></i>
      </div>
      <div v-if="filteredSuggestions.length" class="suggestions">
        <ul>
          <li v-for="suggestion in filteredSuggestions" :key="suggestion">
            {{ suggestion }}
          </li>
        </ul>
      </div>
    </div>

        <!-- CountrySelector -->
        <div class="country-selector">
      <img 
        :src="selectedFlag" 
        alt="Country Flag" 
        class="flag-image"
        @click="toggleDropdown('showDropdown')"
      />
      <div v-if="showDropdown" class="dropdown-menu">
        <div v-for="(flag, country) in countries" :key="country" class="dropdown-item" @click="selectCountry(country)">
  <img :src="flag" alt="Country Flag" class="dropdown-flag" />
  <span>{{ country }}</span>
</div>

      </div>
    </div>
        <!-- Notification -->
        <div class="notification">
      <span class="notification-icon">🔔</span>
      <div class="notification-badge" v-if="notificationCount > 0">
        {{ notificationCount }}
      </div>
    </div>

    <!-- Profile -->
    <div class="profile">
      <img 
        :src="profilePhoto" 
        alt="Profile Photo" 
        class="profile-image"
        @click="toggleProfileDropdown"
      />
      <div v-if="showProfileDropdown" class="profile-dropdown">
        <div class="profile-dropdown-item">Profile</div>
        <div class="profile-dropdown-item">Calendar</div>
        <div class="profile-dropdown-item">Contacts</div>
        <hr />
        <div class="profile-dropdown-item">Logout</div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed } from 'vue';
import profilePhotoSrc from '@/assets/foto.jpeg';
import americaFlag from '@/assets/bayrak/Abd.jpeg';
import italyFlag from '@/assets/bayrak/İtalya.jpeg';
import franceFlag from '@/assets/bayrak/Fransa.jpeg';
import spainFlag from '@/assets/bayrak/İspanya.jpeg';
import chinaFlag from '@/assets/bayrak/Çin.jpeg';
import germanyFlag from '@/assets/bayrak/Almanya.jpeg';
import japanFlag from '@/assets/bayrak/Japonya.jpeg';

interface CountryFlags {
  [country: string]: string;
}


export default defineComponent({
  name: 'SecondaryHeader',
  setup() {
    // SearchComponent
    const query = ref('');
    const suggestions = ref<string[]>([
      'Suggestion 1',
      'Suggestion 2',
      'Suggestion 3',
      'Suggestion 4'
    ]);

    const filteredSuggestions = computed(() =>
      query.value
        ? suggestions.value.filter(suggestion =>
            suggestion.toLowerCase().includes(query.value.toLowerCase())
          )
        : []
    );

    const onSearch = () => {
      if (query.value) {
        alert(`Searched for: ${query.value}`);
      }
    };

    // Notification
    const notificationCount = ref(3);

    // CountrySelector
    const showDropdown = ref<boolean>(false);
    const selectedCountry = ref<string>('USA');
      const countries: CountryFlags = {
  USA: americaFlag,
  Italy: italyFlag,
  France: franceFlag,
  Spain: spainFlag,
  China: chinaFlag,
  Germany: germanyFlag,
  Japan: japanFlag,
};

    const selectedFlag = ref<string>(countries[selectedCountry.value]);

    const toggleDropdown = (dropdown: 'showDropdown') => {
      showDropdown.value = !showDropdown.value;
    };

    const selectCountry = (country: string) => {
      selectedCountry.value = country;
      selectedFlag.value = countries[country];
      showDropdown.value = false;
    };

    // Profile
    const showProfileDropdown = ref<boolean>(false);
    const profilePhoto = ref<string>(profilePhotoSrc);

    const toggleProfileDropdown = () => {
      showProfileDropdown.value = !showProfileDropdown.value;
    };

    return {
      query,
      filteredSuggestions,
      onSearch,
      notificationCount,
      showDropdown,
      selectedFlag,
      countries,
      selectCountry,
      toggleDropdown,
      profilePhoto,
      showProfileDropdown,
      toggleProfileDropdown,
    };
  },
});
</script>

