<template>
  <div class="auth-container forgot-password">
    <img src="../assets/logo.jpeg" class="logo" alt="Logo" />
    <h2>Forgot Password</h2>
    <form @submit.prevent="handleForgotPassword">
      <FormInput id="email" label="Email" type="email" v-model="email" required />
      <button type="submit">Send Reset Link</button>
    </form>
    <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
    <div class="login-link">
      Remember your password? <router-link to="/login">Login</router-link>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import FormInput from '../components/FormInput.vue';

export default defineComponent({
  name: 'ForgotPasswordPage',
  components: { FormInput },
  setup() {
    const email = ref<string>('');
    const errorMessage = ref<string>('');

    const handleForgotPassword = () => {
      if (email.value) {
        alert('Password reset link sent to your email!');
        errorMessage.value = '';
      } else {
        errorMessage.value = 'Please enter your email';
      }
    };

    return {
      email,
      errorMessage,
      handleForgotPassword
    };
  }
});
</script>

