<template>
    <div class="chart-container">
      <canvas ref="lineChart"></canvas>
    </div>
  </template>
  
  <script lang="ts">
  import { defineComponent, onMounted, ref, PropType } from 'vue';
  import { Chart, LineElement, CategoryScale, LinearScale, Title, Tooltip, Legend } from 'chart.js';
  
  Chart.register(LineElement, CategoryScale, LinearScale, Title, Tooltip, Legend);
  
  export default defineComponent({
    name: 'LineChart',
    props: {
      data: {
        type: Array as PropType<number[]>,
        required: true
      }
    },
    setup(props) {
      const lineChart = ref<HTMLCanvasElement | null>(null);
  
      onMounted(() => {
        if (lineChart.value) {
          new Chart(lineChart.value, {
            type: 'line',
            data: {
              labels: Array.from({ length: props.data.length }, (_, i) => `Label ${i + 1}`),
              datasets: [{
                label: 'Line Chart',
                data: props.data,
                borderColor: 'rgba(54, 162, 235, 1)', // Mavi renk
                backgroundColor: 'rgba(54, 162, 235, 0.3)', // Mavi dolgu rengi
                borderWidth: 2,
                fill: true, // Alt alanın doldurulmasını sağlar
                tension: 0, // Daha eğimli geçişler
                pointRadius: 0, // Kırılma noktalarını yuvarlak yapmamak için
              }]
            },
            options: {
              responsive: true,
              maintainAspectRatio: false, // Boyut değişiminde grafik oranını korumaz
              plugins: {
                legend: {
                  display: false
                },
                tooltip: {
                  callbacks: {
                    label: (context) => `Value: ${context.raw}`,
                  }
                }
              },
              scales: {
                x: {
                  display: false, // X eksenini gizler
                  grid: {
                    display: false // X ekseni ızgarasını gizler
                  }
                },
                y: {
                  display: false, // Y eksenini gizler
                  grid: {
                    display: false // Y ekseni ızgarasını gizler
                  },
                  beginAtZero: true // Y ekseninin sıfırdan başlamasını sağlar
                }
              }
            }
          });
        }
      });
  
      return {
        lineChart
      };
    }
  });
  </script>
  
  <style scoped>
  .chart-container {
    position: relative;
    width: 100%;
    height: 100%; /* Kutucuk içindeki yüksekliği tamamen kaplar */
  }
  
  canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; /* Grafik kutucuğun tamamını kaplar */
  }
  </style>
  